@import "./reset.css";

.container {
  height: calc(100vh - 76px - 76px);
  width: 616px;
  margin: 0 auto;
  overflow: scroll;
}

h2{
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0px;
  padding: 30px 0 10px;
}


@media screen and (max-width: 700px) {
  .container {
    margin: 0 42px;
    width: auto
  }
}
