@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
header {
  height: 76px;
  border-bottom: 1px solid #1111111a;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.header_logo {
  margin-left: 51px;
}

.header_nav {
  width: 100%;
  display: flex;
  justify-content: center;
}

.header_link {
  margin: 0 12px;
  color: #1111117a;
  text-decoration: none;
  transition: all 0.3s;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
}

.header_link:hover {
  color: #111111cc;
}

@media screen and (max-width: 470px) {
  .header_logo {
    margin-left: 0;
  }
}

@media screen and (max-width: 390px) {
  .header_link  {
    margin: 0 12px 0 0;
  }
}

footer{
height: 76px;
background: #FAFAFA;
box-shadow: 0px 1px 0px rgba(17, 17, 17, 0.1);
display: flex;
justify-content: center;
align-items: center;
}

.footer_text{
font-size: 13px;
font-style: normal;
font-weight: 400;
line-height: 16px;
letter-spacing: 0px;
}

.ui_input_container{
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  margin: 5px 0 ;
}

.ui_label{
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0px;  
  color: #1111117A;
  position: absolute;
  left: 16px;
  top: 8px;
}

.ui_input {
background: #FFFFFF;
border: 1px solid #F1F1F1;
border-radius: 4px;
height: 56px;
max-width: 616px;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 24px;
letter-spacing: 0px;
padding-left: 16px;
padding-top: 20px;
transition: 0.3s;
}

.ui_input:hover, .ui_input:focus {
  border: 1px solid #6e6e6e !important;
}

.ui_input::-webkit-outer-spin-button,
.ui_input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.ui_input[type=number] {
  -moz-appearance: textfield;
}

.ui_button {
  border: none;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
  background: white;
  color: #01a7fd;
  transition: 0.3s;
  display: flex;
  align-items: center;
  height: 44px;
}

.ui_button:hover {
  color: #df0000;
}

.border {
  border: 2px solid #01a7fd;
  border-radius: 100px;
  padding:  10px 20px;
} 
.border:hover {
  color: #01a7fd;
  background-color: rgb(236, 251, 253);
}

.blue{
  padding:  10px 20px;
  background: #01a7fd;
  color: white;
  border-radius: 100px;
}

.blue:hover {
  color: white;
  background: #df0000;
}

.children_form {
  display: grid;
  grid-template-columns: 1fr 1fr auto;
  grid-gap: 18px;
  align-items: center;
}

@media screen and (max-width: 529px) {
  .children_form  {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0;
  }
}

.form_person_data {
  display: flex;
  flex-direction: column;
}
.add_children_block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 18px;
}


.add_data_block {
  margin-top: 25px;
}

.sross_img {
  margin: 0 8px 0 4px;
}

@media screen and (max-width: 529px) {
  .add_data_block {
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 470px) {
  .add_children_button_name  {
    display: none;
  }
}

.person_data_block {
  margin-top: 20px;
}

.person_data {
  margin-top: 20px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0px;
}

.children_data_block{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.child_card {
  height: 44px;
  padding: 10px 20px;
  background: #f1f1f1;
  border-radius: 5px;
  margin: 10px;
  margin-right: auto;
}

.child_data {
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0px;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
*.ui_input :active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}

.container {
  height: calc(100vh - 76px - 76px);
  width: 616px;
  margin: 0 auto;
  overflow: scroll;
}

h2{
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0px;
  padding: 30px 0 10px;
}


@media screen and (max-width: 700px) {
  .container {
    margin: 0 42px;
    width: auto
  }
}

